import { useState } from 'react';
import styles from './login.module.css';
import { Button, EmailInput } from '@ya.praktikum/react-developer-burger-ui-components';
import { Link, useNavigate } from 'react-router-dom';
import { recoveryPassword } from '../utils/burger-api';

export function ForgotPassword() {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [error, setError] = useState(false);

  const onChangeEmail = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setEmail(e.target.value)
  }

  const onSubmitHandler = async () => {
    await recoveryPassword(email).then((resp: any) => {
      if (resp.success) {
        navigate("/reset-password", { state: { email } });
        setError(false)
      }
    }, () => {
      setError(true)
    });
  }

  return (
    <>
      <div className={styles.wrapper}>
        <div className={styles.content}>
          <div className="title text text_type_main-medium mb-6"> </div>
          <div className="form">
            <div className='mb-6'>
              <EmailInput
                onChange={onChangeEmail}
                value={email}
                placeholder=' Email'
              />
            </div>
            <div className={`mb-20 ${styles.title}`}>
              <Button htmlType="button" onClick={onSubmitHandler}></Button>
            </div>
          </div>
          {error ? (
            <p className='text text_type_main-default'> </p>
          ) : ''}
          <div className="actions">
            <div className={styles.title}>
              <span className='text_color_inactive text text_type_main-default mr-1'> ?</span>
              <Link to="/login" className='text text_type_main-default text_color_accent'></Link>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}